var setVal, tLbl, twoDec, validate, validateBtn;

setVal = function() {
  var base_price, f_price, in_dep, in_vol, ref, side, u_price;
  side = $('#oSide').html();
  in_dep = parseInt($('#inDep').val());
  in_vol = parseInt($('#inVol').val());
  validateBtn(in_dep, in_vol);
  tLbl(in_dep, in_vol);
  base_price = twoDec(in_dep / in_vol);
  ref = (function() {
    switch (side) {
      case "fixed":
        return [base_price, twoDec(1.0 - base_price)];
      default:
        return [twoDec(1.0 - base_price), base_price];
    }
  })(), f_price = ref[0], u_price = ref[1];
  $('.dVol').html(in_vol);
  $('.dPro').html(in_vol - in_dep);
  $('.dF').html(f_price);
  return $('.dU').html(u_price);
};

validate = function(stake, volume) {
  if (!(volume > 0)) {
    return [false, "Volume must be greater than 0"];
  }
  if (!(stake > 0)) {
    return [false, "Deposit must be greater than 0"];
  }
  if (!(stake < volume)) {
    return [false, "Deposit must be less than volume"];
  }
  return [true, "ok"];
};

validateBtn = function(in_dep, in_vol) {
  var msg, ref, valid;
  ref = validate(in_dep, in_vol), valid = ref[0], msg = ref[1];
  if (valid) {
    $('#errMsg').html("");
    return $('#sBtn').removeClass("disabled").removeClass("strikeout");
  } else {
    $('#errMsg').html(msg);
    return $('#sBtn').addClass("disabled").addClass("strikeout");
  }
};

tLbl = function(stake, vol, pro) {
  var sp, ss, sv;
  ss = stake === 1 ? "" : "s";
  sv = vol === 1 ? "" : "s";
  sp = vol === 1 ? "" : "s";
  $('#sLbl').html(ss);
  $('#vLbl').html(sv);
  return $('#pLbl').html(sv);
};

twoDec = function(num) {
  return Math.round(num * 100) / 100;
};

$(document).ready(function() {
  setVal();
  return $('.token-field').keyup(function() {
    return setVal();
  });
});


